<template>
	<div class="box">
		<div class="headline">
      <div class="go-back">
        <img src="../../public/img/detail-page03.png" alt>
        <span @click="upOne">返回</span>
      </div>
      <span class="book-details">与“{{name}}”有关的书籍</span>
      <div class="homepage">
        <img src="../../public/img/detail-page02.png" @click="homepage">
      </div>
    </div>

		<div class="books">
			<div class="book" v-for="(item, index) in datas" :key="index"  @click="goDetails(item)">
				<div class="img">
					<img :src="setDatum(item.cover)">
				</div>
				<dir class="right">
					<h4>{{item.title}}</h4>
					<div class="author">
						{{item.author}}
            <span>|</span>
            {{item.cat}}
					</div>
					<div class="synopsis">
						<span>{{item.shortIntro}}</span>
					</div>
					<div class="reader">
						<span>{{item.latelyFollower}}</span>人气
						<span class="upright">|</span>
						<span>{{item.retentionRatio}}%</span>读者留存
					</div>
				</dir>
			</div>
      <p>没有更多书籍</p>
		</div>
	</div>
</template>

<script>
export default {
  name: "RelatedBook",
  //name==> 书名
  props: ["name"],
	data() {
		return {
      datas: "",
		}
	},
	methods: {
		upOne() {
      this.$router.push({path: '/Seek'})
    },
		homepage() {
      this.$router.push({path: "/"})
		},
		setDatum(url) {
      //图片路径处理
      if(url) {
        let imgurl = url.replace("/agent/", "");
        imgurl = unescape(imgurl);
        return imgurl;
      }
    },
    goDetails(item) {
      this.$router.push({path: "/Details", query: {name: item}})
    }
	},
	created() {
    // window.location.reload()
    console.log(this.name)
    let bookurl = "api/book/fuzzy-search?query=" + this.name
		this.axios.get(bookurl).then(
			response => {
				this.datas = response.data.books
				console.log(this.datas)
			}
		)
  },
	
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
@import "../../public/sass/mixin";

.box {
	margin-top: -100px;
  z-index: 999;
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 120%;
  background: #fff;
}

.box .headline {
  display: flex;
  width: 100%;
  height: 44px;
  background: #b93321;

  .go-back {
    flex: 0 0 60px;
    width: 60px;
    height: 100%;
    line-height: 44px;

    img {
      margin-left: 10px;
      width: 15px;
    }

    span {
      display: inline-block;
      margin-top: -3px;
      vertical-align: top;
      font-size: 12px;
      font-weight: 400;
      color: #fff;
    }
  }

  .book-details {
    flex: 1;
    text-align: center;
    line-height: 44px;
    font-size: 14px;
    color: #fff;
  }

  .homepage {
    flex: 0 0 50px;
    width: 50px;
    height: 100%;
    text-align: center;
    line-height: 44px;

    img {
      width: 15px;
    }
  }
}

.box .books {
  width: 100%;
  background: #f5f2ed;
}

.box .books .book {
  display: flex;
  margin-left: 15px;
  padding: 15px 15px 15px 0px;
  width: calc(100% - 30px);
  height: 75px;
  @include border-bottom(#ccc);

  .img {
    display: inline-block;
    flex: 0 0 60px;
    width: 60px;
    height: 75px;
    border: 1px solid #ebebeb;
    box-sizing: border-box;

    img {
      width: 100%;
      height: 100%;
    }
  }
}
.box .books .book .right {
  flex: 0 1 calc(100% - 75px);
  width: calc(100% - 75px);
  margin: 0;
  margin-left: 15px;
  padding: 0;

  h4 {
    height: 20px;
    line-height: 20px;
    font-size: 14px;
    font-weight: bold;
    color: #333 !important;
  }

  .author {
    height: 20px;
    line-height: 20px;
    font-size: 12px;
    color: #999;

    span {
      padding: 0 10px;
    }
  }

  .synopsis {
    height: 20px;
    line-height: 20px;
    font-size: 12px;
    color: #999;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden; 

    span {
      display: inline-block;
      width: 100%; 
    }
  }

  .reader {
    height: 20px;
    line-height: 20px;
    font-size: 12px;
    color: #666;

    span {
      color: #b93321;
    }

    span.upright {
      color: #666;
      padding: 0 10px;
    }
  }
}

.box .books > p {
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 12px;
  color: #999;
}

</style>
